body {
    /* font-family: 'Roboto Mono', monospace; */
    /* font-family: 'Poppins', sans-serif; */
    /* font-family: "Helvetica Neue", Helvetica; */
    font-family: 'Lexend', sans-serif;
    -webkit-font-smoothing: antialiased;
    line-height: 1.4rem;
    margin: 0 auto;
    max-width: 768px;
    color: #222;
    background-color: rgb(206, 226, 210);
    font-weight: lighter;
}

.headshot {
    width: 350px;
    height: 350px;
    float: right;
    margin: 0 0 0 0px;
}

.headshote {
    display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

#content {
    margin: 1rem;
}

h1 {
    /* font-family: 'Roboto Mono', monospace; */
    /* font-family: "Helvetica Neue", Helvetica; */
    font-size: 3rem;
    font-weight: bold;
    line-height: 3rem
}

h2 {
    line-height: 1.5rem;
}

a,
a:visited {
    color: #009a64;
}

.collapsible {
  cursor: pointer;
}

.collapsible:hover {
  text-decoration: underline;
}


